<template>
	<view class="width-000">
		<!-- 类型选择 -->
		<view class="width-000">
			<view class="position-relative width-000">
				<view style="box-sizing: border-box;" class=" seckill-title  px-3  d-flex a-center width-000">
					<view @tap="typeOpt(0)"  v-if="type == 0 || type == 'def' || !isService" class="d-flex a-center" :style="titleStyle0" style="width: 50%;">
						<view class="color-fff leftTitle"  :style="titleStyle0" style="height: 46rpx !important;">
							武汉市全城
						</view>
						<view :class="currentType == 0?'iconshangjiantou':'iconxia'" class="iconfont  color-fff font-27 ml-2" style="opacity: 0.6;">
							
						</view>
					</view>
					
					<view class=""  style="width: 50%;">
						<view @tap="typeOpt(1)" v-if="type == 1 || type == 'def'" :style="titleStyle1" class="d-flex a-center " style="width: 100%; ">
							<view class="d-flex a-center color-fff leftTitle" style="" :style="titleStyle1">
								门店主题
							
							</view>
							<view :class="currentType == 1?'iconshangjiantou':'iconxia'" class="iconfont color-fff font-27 ml-2" style="opacity: 0.6;">
								
							</view>
						</view>
					</view>
					<!-- <view class="position-absolute" style="top: 50%;left: 50%;transform: translate(-50%,-50%);height: 100rpx;width: 5rpx;background-color: #ccc;">
						
					</view> -->
					<view class="" style="width: 50%;" v-if="isService" >
						<view class="d-flex a-center" :style="titleStyle0">
							<view class="iconfont iconkefu color-fff mr-2">
								
							</view>
							<view style="font-size: 32rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;">
								联系客服
							</view>
						</view> 
					</view>
				
				
				</view>
				<view class="position-absolute width-000 z-index-1 pb-2" v-show="currentType == 0">
					<view class="d-flex">
						<view class="theme-bg d-flex j-center a-center flex-column pt-2" style="width: 30%;">
							<view class="mb-2 height-100 d-flex a-center j-center width-000"  @tap="optCity(item,index)" :class="citySelectCurrent == index?'citySelectCurrent':'citySelectCurrentNo'" v-for="(item,index) in citySelect" :key="index">
								{{item.name}}
							</view>
						</view>
						<view class="d-flex flex-wrap" style="width: 70%;background: #803667;">
							<view @tap="optCity1(item,index)" class="mx-2 mt-3 d-flex a-center j-center" :class="item.activate?'activate':'activateNo'" v-for="(item,index) in citySelect[citySelectCurrent].chid" :key="index">
								{{item.name}}
							</view>
						</view>
					</view>
				</view>
				
				
				<view class="mt-2 position-absolute width-000 z-index-1 " style="background: #803667;"  v-show="currentType == 1">
					<view class="d-flex a-center j-center flex-column">
						<view @tap="shopCut(item,index)" class="d-flex a-center j-center mb-2" :class="shopThemeList.current == index?'current':'currentNo'" v-for="(item,index) in shopThemeList.list" :key="index">
							<view class="" style="font-size: 27rpx;font-family: PingFang SC;font-weight: 400;color: #FFFFFF;">
								{{item}}
							</view>
						</view>
					</view>
				</view>
							
			</view>
			
			
		</view>
		<!-- 类型选择 -->
	
	</view>
</template>

<script>
	export default {
		data(){
			return {
				currentType:-1,
				citySelectCurrent:0,
				citySelect:[
					{
						name:"武汉市",
						chid:[
							{
								name:'武汉市',
								activate:true
							},
							{
								name:'武汉市',
								activate:false
							}
						]
					},
					{
						name:"武汉市",
						chid:[
							{
								name:'1111',
								activate:false
							},
							{
								name:'武汉市',
								activate:false
							}
						]
					},
					{
						name:"武汉市",
						chid:[
							{
								name:'武汉市',
								activate:false
							},
							{
								name:'2222',
								activate:false
							}
						]
					}
				],
				
				
				shopThemeList:{
					current:0,
					list:[
						'全部',
						'直营全能店',
						'合作服务商',
						'LOFT店'
					]
				}
				
			}
		},
		props:{
			isService:{
				type:Boolean,
				default:false
			},
			type:{
				type:String,
				default:'def'
			},
			titleStyle0:{
				type:String,
				default:''
			},
			titleStyle1:{
				type:String,
				default:''
			}
		},
		methods:{
			optCity1(item,index){
				this.citySelect.forEach(item=>{
					item.chid.forEach((item1,index)=>{
						item1.activate = false
					})
				})
				item.activate = !item.activate
				console.log(item,index)
			},
			optCity(item,index){
				this.citySelectCurrent = index
			},
			typeOpt(type){
				if(type == this.currentType){
					this.currentType = -1
					return
				}
				this.currentType = type
			},
			shopCut(item,index){
				this.shopThemeList.current = index
			}
		}
	}
</script>

<style>
	.activate{
		width: 120rpx;
		height: 60rpx;
		background: #FF2F68;
		border-radius: 10rpx;
		font-size: 32rpx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
	}
	.activateNo{
		width: 120rpx;
		height: 60rpx;
		font-size: 32rpx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
	}
	.citySelectCurrent{
		
		font-size: 32rpx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #FF2F68;
		
	}
	.citySelectCurrentNo{
		
		font-size: 32rpx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #FFFFFF;
	}
	.current{
		width: 500rpx;
		height: 80rpx;
		background: #FF2F68;
		border: 1rpx solid #FF2F68;
		border-radius: 10rpx;
	}
	.currentNo{
		width: 500rpx;
		height: 80rpx;
		border: 1rpx solid #8645FF;
		border-radius: 10rpx;
	}
</style>
